*,
*::after,
*::before,
p,
span {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  line-height: inherit;
  font-size: inherit;
}

:root {
  --main-text: #0E0E0E;
  --muted-text: #7D7D7D;
  --white-text: #ffffff;
  --black: #0E0E0E;
  --gray-bg: #F5F5F7;
  --gray-hover-bg: #E1E1E3;
  --black-bg: #0E0E0E;
  --blue: #5AA0F5;
}

body {
  font-family: "Inter", sans-serif;
  color: var(--main-text);
  font-size: 16px;
  font-weight: 400;
  box-sizing: border-box;
}

.container {
  max-width: 1514px;
  width: calc(100% - 40px);
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  background-color: var(--black);
  color: var(--white-text);
  padding: 19px 0;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn__back_link {
  display: flex;
  gap: 6px;
  align-items: center;
}
.btn__back_link.btn__back_link-mobile {
  display: none;
  font-size: 0;
}

.header__logo {
  font-size: 0;
}

.btn__link {
  padding: 0 28.5px;
  border: 2px solid #5B5B6A;
  border-radius: 8px;
  line-height: 48px;
  text-align: center;
}
.btn__link.btn__link-mobile {
  display: none;
  border: none;
  padding: 0;
  font-size: 0;
  line-height: 1;
}

@media (max-width: 1440px) {
  .container {
    max-width: 1346px;
    width: calc(100% - 40px);
    margin: 0 auto;
    padding: 0 20px;
  }
}
@media (max-width: 1024px) {
  .container {
    width: calc(100% - 40px);
    padding: 0 20px;
  }
}
@media (max-width: 768px) {
  .header {
    padding: 21.5px 0;
  }
  .btn__back_link {
    display: none;
  }
  .btn__back_link.btn__back_link-mobile {
    display: block;
  }
  .header__logo img {
    height: 42px;
  }
  .btn__link {
    display: none;
  }
  .btn__link.btn__link-mobile {
    display: block;
  }
}
@media (max-width: 375px) {
  .header {
    padding: 12px 0;
  }
  .header__logo img {
    height: 23px;
  }
  .btn__back_link-mobile {
    margin-left: 8px;
  }
  .btn__link-mobile {
    margin-right: 8px;
  }
}